.cart-wrapper{display:flex;justify-content:space-between;align-items:center;padding:5px 8px;border-bottom:1px solid gray}.cart-wrapper .cart-heading{font-family:"HM Sans Semi Bold","HMSansHebrew-SemiBold","ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic Pro",Osaka,"メイリオ",Meiryo,"ＭＳ Ｐゴシック","MS PGothic",sans-serif;font-weight:500;line-height:1.142857143;font-size:1.75rem}.cart-wrapper .image img{border-radius:50%;padding:0px 28px 0px 0px}.cart-wrapper .heading-container{position:relative;right:32px}.cart-wrapper .heading-container .product-name{font-size:16px;font-weight:700;width:225px;font-family:"HM Sans Semi Bold","HMSansHebrew-SemiBold","ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic Pro",Osaka,"メイリオ",Meiryo,"ＭＳ Ｐゴシック","MS PGothic",sans-serif}.cart-wrapper .heading-container .product-category{font-size:12px;font-weight:600;font-family:"HM Sans Semi Bold","HMSansHebrew-SemiBold","ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic Pro",Osaka,"メイリオ",Meiryo,"ＭＳ Ｐゴシック","MS PGothic",sans-serif}.cart-wrapper .qty-container{display:flex;justify-content:center;align-items:center;column-gap:15px;width:70px}.cart-wrapper .qty-container .number{border:1.5px solid #000;width:16px;font-size:13px;text-align:center;font-weight:700;font-family:"HM Sans Semi Bold","HMSansHebrew-SemiBold","ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic Pro",Osaka,"メイリオ",Meiryo,"ＭＳ Ｐゴシック","MS PGothic",sans-serif}.cart-wrapper .qty-container .decrease-icon{cursor:pointer}.cart-wrapper .qty-container .increase-icon{cursor:pointer}.cart-wrapper .price{width:70px}.cart-wrapper .dustbin-icon{cursor:pointer}footer{position:fixed;bottom:10px;display:flex;justify-content:center;align-items:center;column-gap:10px}footer .checkout-btn{padding:15px 75px;font-size:15px;font-weight:600;color:#fff;background-color:#000;cursor:pointer}footer .cart-total{border:2px solid #000;text-align:center;width:276px;height:47px;font-size:15px;font-weight:600;color:#fff;background-color:#000}