.navbar{display:flex;justify-content:space-between;align-items:center;background-color:#000;height:50px;padding:0px 30px;position:fixed;top:0;width:1459px;z-index:100}.navbar .nav-options{display:flex;justify-content:center;align-items:center;column-gap:20px}.navbar .nav-options .cart{color:#fff;height:25px;width:25px;cursor:pointer}.navbar .nav-options .wishlist{color:#fff;height:25px;width:25px;cursor:pointer}.navbar .nav-options .search-container{display:flex;column-gap:-10px;border-bottom:1px solid #fff}.navbar .nav-options .search-container .search-bar{outline:none;background-color:rgba(0,0,0,0);border:none;color:#fff;font-size:20px}.navbar .nav-options .search-container .search-icon{color:#fff;height:25px;width:25px;cursor:pointer}.navbar h3{color:#fff;color:red;font-size:1.5rem;font-family:"HM Sans Semi Bold","HMSansHebrew-SemiBold","ヒラギノ角ゴ Pro W3","Hiragino Kaku Gothic Pro",Osaka,"メイリオ",Meiryo,"ＭＳ Ｐゴシック","MS PGothic",sans-serif}